import React, {useState, useEffect} from 'react';
import SweetAlert from 'react-bootstrap-sweetalert'
import ContainerHeader from 'components/ContainerHeader/index';
import IntlMessages from 'util/IntlMessages';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import SiteTable from "./SiteTable"
import RoomTable from "./RoomTable";
import TaskTable from "./TaskTable";
import ProductTable from "./ProductTable";
import {base_url, public_url, $http} from "config";
import {useSelector} from 'react-redux';
import CircularProgress from '@material-ui/core/CircularProgress';
import {useHistory} from 'react-router-dom';
import {Alert} from 'reactstrap';
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import PlaylistPlayIcon from "@material-ui/icons/PlaylistAdd";
import {NotificationContainer, NotificationManager} from 'react-notifications';
import {Modal, ModalHeader} from 'reactstrap';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import {KeyboardDatePicker, MuiPickersUtilsProvider} from '@material-ui/pickers';
import DateFnsUtils from "@date-io/date-fns";
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import Input from '@material-ui/core/Input';
import AttachFile from '@material-ui/icons/AttachFile';
import CloseIcon from '@material-ui/icons/Close';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormLabel from '@material-ui/core/FormLabel';
import FormControl from '@material-ui/core/FormControl';
import Fab from '@material-ui/core/Fab';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;

const SiteSurveyInfo = (props) => {
    
    const project_id = props.location.state.id;
    const [Project, setProject] = useState(null);
    //const [Users, setUsers] = useState([]);
    const [Sites, setSites] = useState([]);
    const [Rooms, setRooms] = useState([]);
    //const [Projects, setProjects] = useState([]);
    const [Tasks, setTasks] = useState([]);
    const [Products, setProducts] = useState([]);
    const [warning, setWarning]=useState(false);
    const [loader, setLoader] = useState(false);
    const History = useHistory();
    const {width} = useSelector(({settings}) => settings);

    const [site_open, setSiteOpen] = useState(false);
    const [site_id, setSiteId] = useState(null);
    const [project_site, setProjectSite] = useState("");
    const [site_date, setSiteDate] = useState(new Date());
    const [site_rows, setSiteRows] = useState([]);

    const [room_open, setRoomOpen] = useState(false);
    const [room_id, setRoomId] = useState(null);
    const [room_site, setRoomSite] = useState(0);
    const [room_project, setRoomProject] = useState(0);
    const [room_number, setRoomNumber] = useState("");
    const [room_height, setRoomHeight] = useState("");
    const [room_ceiling, setRoomCeiling] = useState("");
    const [room_wall, setRoomWall] = useState("");
    const [room_day, setRoomDay] = useState(0);
    const [room_hour, setRoomHour] = useState(0);
    const [room_img, setRoomImg] = useState([]);
    const [room_img_len, setRoomImgLen] = useState(0);
    const [room_img_url, setRoomImgUrl] = useState([]);
    const [room_asbestos, setRoomAsbestos] = useState("1");
    const [room_notes, setRoomNotes] = useState("");
    const [room_site_rows, setRoomSiteRows] = useState([]);
    const [room_project_rows, setRoomProjectRows] = useState([]);
    const [room_departmentname_rows, setRoomDepartmentRows] = useState([]);
    const [room_buildingname_rows, setRoomBuildingRows] = useState([]);
    const [room_floorname_rows, setRoomFloorRows] = useState([]);
    const [room_department_id, setRoomDepartmentName] = useState(null);
    const [room_building_id, setRoomBuildingName] = useState(null);
    const [room_floor_id, setRoomFloorName] = useState(null);

    const [task_open, setTaskOpen] = useState(false);
    const [task_id, setTaskId] = useState(null);
    const [task_sitename, setTaskSiteName] = useState("");
    const [task_project, setTaskProject] = useState("");
    const [task_room, setTaskRoom] = useState("");
    const [task_assignto, setTaskAssignto] = useState([]);
    const [task_duebydate, setTaskDuebydate] = useState(new Date());
    const [task_title, setTaskTitle] = useState("");
    const [task_des, setTaskDes] = useState("");
    const [task_img, setTaskImg] = useState(null);
    const [TaskImgUrl, setTaskImgUrl] = useState('');
    const [task_priority, setTaskPriority] = useState(0);
    const [task_sitename_rows, setTaskSiteNames] = useState([]);
    const [task_project_rows, setTaskProjects] = useState([]);
    const [task_room_rows, setTaskRooms] = useState([]);
    const [task_assignto_rows, setTaskAssigntos] = useState([]);

    const [product_open, setProductOpen] = useState(false);
    const [product_id, setProductId] = useState(null);
    const [product_name, setProductName] = useState("");
    const [product_roomname_rows, setProductRoomNames] = useState([]);
    const [product_room_id, setProductRoomId] = useState("");
    const [product_action, setProductAction] = useState("");
    const [product_description, setProductDescription] = useState('');
    const [product_qty, setProductQty] = useState('');
    const [product_sitename_rows, setProductSiteNames] = useState([]);
    const [product_tosite_id, setProductToSiteId] = useState("");
    const [product_toroom_id, setProductToRoomId] = useState("");
    const [product_file, setProductFile] = useState(null);
    const [product_fileurl, setProductFileUrl] = useState("");
    
    
    const task_priority_rows = ([
        {id: 0, content: 'Low'},
        {id: 1, content: 'High'}
    ]);
    const product_action_rows = ([
        {id: 0, content: 'New Product'},
        {id: 1, content: 'Dispose'},
        {id: 2, content: 'Move To Room'}
    ]);
    const uploadMultipleFiles=(e)=>{
        var fileObj = [];
        var img_url = [...room_img_url]
        fileObj.push(e.target.files);
        for (let i = 0; i < fileObj[0].length; i++) {
            img_url.push(URL.createObjectURL(fileObj[0][i]));
        }
        setRoomImgUrl(img_url);
        fileObj = [...room_img];
        for(let i = 0; i < e.target.files.length; i++) {
            fileObj.push(e.target.files[i]);
        }
        setRoomImg(fileObj);
        console.log(room_img);
    }
    const removeImage=(item)=>{
        var img_url = [...room_img_url]
        img_url.splice(item, 1)
        setRoomImgUrl(img_url);
        if(item < room_img_len)
            setRoomImgLen(room_img_len-1);
        else{
            img_url = [...room_img]
            img_url.splice(item-room_img_len, 1)
            setRoomImg(img_url);
        }
    }

    useEffect(() => {
        Init_data();
    }, [project_id]);

    const Init_data = () => {
        if (project_id > 0) {
            $http.get(base_url + "project/projectInfo", {params : {id: project_id}})
            .then((response) => {
                setProject(response.data.project);
                setRooms(response.data.rooms);
                setSites(response.data.sites);
                setTasks(response.data.tasks);
                setProducts(response.data.products);
                setSiteRows(response.data.customer_sites);
                setLoader(true);
            });
        }
    }
    
    const ClickModify = () => {
        History.push({
            pathname: '/app/sitesurvey/add-new',
            state: {
                id: project_id
            }
        })
    }
    const deleteFile = () => {
        if (project_id > 0) {
            $http.post(base_url + "sitesurvey/DeleteCompanyCustomer", {id: project_id})
            .then((response) => {
                History.push('/app/sitesurvey/live');
            })
        }
        setWarning(false);
    };
    const onCancelDelete = () => {
        setWarning(false);
    };

    const onSelectSite = (data, index) => {
        if (index === 1) {
            SiteModalOpen(data);
        } else if (index === 2) {
            $http.post(base_url + "site/deleteSite", {id: data})
            .then((response) => {
                if (response.data.status==="success") {
                    NotificationManager.info("You removed selected site");
                    Init_data();
                }
            })
        }
    }

    const onSelectRoom = (data, index) => {
        if(index ===0){
            History.push({
                pathname: '/app/sitesurvey/live/roomInfo',
                state: {
                    project_id: project_id,
                    id: data
                }
            })
        }
        else if (index === 1) {
            History.push({
                pathname: '/app/sitesurvey/live/room',
                state: {
                    project_id: project_id,
                    id: data
                }
            })
    
            // RoomModalOpen(data);
        } else if (index === 2) {
            $http.post(base_url + "room/deleteRoom", {id: data})
            .then((response) => {
                if (response.data.status==="success") {
                    NotificationManager.info("You removed selected room");
                    Init_data();
                }
            })
        }
    }

    

    const SiteModalOpen = (data) => {
        setSiteId(data);
        setSiteOpen(true);
        if (data > 0) {
            $http.get(base_url + "projectsite/siteInfo", {params : {id: data}})
            .then((response) => {
                setProjectSite(response.data.site.site_id, data);
                setSiteDate(new Date(response.data.site.survey_date));
            })
        }
    }

    const SiteModalClose = () => {
        setSiteOpen(false);
    }

    const SiteModalSave = () => {
        SiteModalClose();

        const frmData = new FormData();
        frmData.append('site_id', project_site);
        frmData.append('survey_date', 
            site_date.getFullYear()+"-"+
            (site_date.getMonth()+1)+"-"+
            site_date.getDate());


        if (project_id) {
            frmData.append('project_id', project_id);
        }
        if (site_id > 0) {
            frmData.append('id', site_id);
        }

        $http.post(base_url + "projectsite/updateSite", frmData)
        .then((response) => {
            if (response.data.status === "success") {
                NotificationManager.success(response.data.msg, "Success!", 5000, () => {
                });
                Init_data();
            } else if (response.data.status === "error") {
                NotificationManager.error(response.data.msg, "Error!", 5000, () => {
                });
            }
        }).catch((error)=>{
            console.log("error:", error);
        })

    }

    const RoomModalOpen = (data) => {
        setRoomId(data);
        $http.get(base_url + "room/roomInfo", {params: {id: data, project_id: project_id}})
        .then((response) => {
            if (response.data) {
                setRoomSiteRows(response.data.sites)
                setRoomProjectRows(response.data.projects)
                setRoomDepartmentRows(response.data.departments)
                setRoomBuildingRows(response.data.buildings)
                setRoomFloorRows(response.data.floors)
                var img_url = []
                if (data > 0) {
                    setRoomImg([]);
                    setRoomSite(response.data.room.site_id);
                    setRoomProject(response.data.room.project_id);
                    setRoomDepartmentName(response.data.room.department_id);
                    setRoomBuildingName(response.data.room.building_id);
                    setRoomFloorName(response.data.room.floor_id);
                    setRoomNumber(response.data.room.room_number);
                    setRoomDay(response.data.room.estimate_day);
                    setRoomHour(response.data.room.estimate_time);
                    setRoomHeight(response.data.room.ceiling_height);
                    setRoomCeiling(response.data.room.ceiling);
                    setRoomWall(response.data.room.wall);
                    setRoomAsbestos(response.data.room.asbestos);
                    setRoomNotes(response.data.room.notes);
                    if(response.data.room.img_files.length > 0){
                        setRoomImgLen(response.data.room.img_files.length);
                        (response.data.room.img_files).map(url =>{
                            img_url.push(public_url + 'upload/img/' + url.img_name);                            
                        })
                    }
                    setRoomImgUrl(img_url);
                    
                }
            }
            setRoomOpen(true);
        })

    }

    const RoomModalClose = () => {
        setRoomOpen(false);
        setRoomImgUrl([])
    }

    const RoomModalSave = () => {
        RoomModalClose();
        const frmData = new FormData();
        frmData.append('project_id', project_id);
        frmData.append('id', room_id);
        frmData.append('site_id', room_site);
        frmData.append('department_id', room_department_id);
        frmData.append('building_id', room_building_id);
        frmData.append('floor_id', room_floor_id);
        frmData.append('room_number', room_number);
        frmData.append('estimate_day', room_day);
        frmData.append('estimate_time', room_hour);
        frmData.append('ceiling_height', room_height);
        frmData.append('ceiling', room_ceiling);
        frmData.append('wall', room_wall);
        for (var i = 0; i < room_img.length; i++)
            frmData.append('room_img['+i+']', room_img[i]);
        frmData.append('img_array', room_img_url);
        frmData.append('asbestos', room_asbestos);
        frmData.append('notes', room_notes);
        if (project_id) {
            frmData.append('project_id', project_id);
            frmData.append('customer_id', Project.company_id);
        }

        $http.post(base_url + "room/updateRoom", frmData)
        .then((response) => {
            if (response.data.status === "success") {
                NotificationManager.success(response.data.msg, "Success!", 5000, () => {
                });
                Init_data();
            } else if (response.data.status === "error") {
                NotificationManager.error(response.data.msg, "Error!", 5000, () => {
                });
            }
        }).catch((error)=>{
            console.log("error:", error);
        })
    }

    const onSelectTask = (data, index) => {
        if (index === 1) {
            TaskModalOpen(data);
        } else if (index === 2) {
            $http.post(base_url + "task/deleteTask", {id: data})
            .then((response) => {
                if (response.data.status==="success") {
                    NotificationManager.info("You removed selected task");
                    Init_data();
                }
            })
        }
    }

    const TaskModalOpen = (data) => {
        setTaskId(data);
        $http.get(base_url + "task/getTaskInfo", {params : {id: data, project_id: project_id}})
        .then((response) => {
            setTaskSiteNames(response.data.customer_site);
            setTaskProjects(response.data.project);
            setTaskRooms(response.data.room);
            setTaskAssigntos(response.data.assign_to);
            if (data > 0) {
                setTaskSiteName(response.data.task.site_id);
                setTaskProject(response.data.task.project_id);
                setTaskRoom(response.data.task.room_id);
                setTaskAssignto(response.data.task.assign_to);
                setTaskTitle(response.data.task.task);
                setTaskDes(response.data.task.description);
                setTaskPriority(response.data.task.priority);
                setTaskDuebydate(new Date(response.data.task.due_by_date));
                setTaskImgUrl(response.data.task.task_img );
            }
           
            setTaskOpen(true);
        })
    }

    const TaskModalClose = () => {
        setTaskOpen(false);
    }

    const TaskModalSave = (data) => {
        TaskModalClose();
        setTaskId(data);
        const frmData = new FormData();

        frmData.append('id', task_id);
        frmData.append('site_id', task_sitename);
        frmData.append('project_id', task_project);
        frmData.append('room_id', task_room);
        frmData.append('assign_to', JSON.stringify(task_assignto));
        frmData.append('task', task_title);
        frmData.append('description', task_des);
        frmData.append('task_img', task_img);
        frmData.append('priority', task_priority);
        frmData.append('due_by_date', 
            task_duebydate.getFullYear()+"-"+
            (task_duebydate.getMonth()+1)+"-"+
            task_duebydate.getDate());
        if (project_id) {
            frmData.append('project_id', project_id);
            frmData.append('company_id', Project.company_id);
        }

        $http.post(base_url + "task/updateTask", frmData)
        .then((response) => {
            if (response.data.status === "success") {
                NotificationManager.success(response.data.msg, "Success!", 5000, () => {
                });
                Init_data();
            } else if (response.data.status === "error") {
                NotificationManager.error(response.data.msg, "Error!", 5000, () => {
                });
            }
        }).catch((error)=>{
            console.log("error:", error);
        })
    }

    const onSelectProduct = (data, index) => {
        if (index === 1) {
            ProductModalOpen(data);
        } else if (index === 2) {
            $http.post(base_url + "product/deleteProduct", {id: data})
            .then((response) => {
                if (response.data.status==="success") {
                    NotificationManager.info("You removed selected task");
                    Init_data();
                }
            })
        }
    }

    const ProductModalOpen = (data) => {
        setProductId(data);
        $http.get(base_url + "product/getProductInfo", {params : {id: data, project_id: project_id}})
        .then((response) => {
            setProductSiteNames(response.data.sites);
            setProductRoomNames(response.data.rooms);
            if (data > 0) {
                setProductName(response.data.product.product_name);
                setProductRoomId(response.data.product.room_id);
                setProductAction(response.data.product.action);
                setProductQty(response.data.product.qty);
                setProductDescription(response.data.product.description);
                setProductToRoomId(response.data.product.to_room_id);
                setProductToSiteId(response.data.product.to_site_id);
                setProductFileUrl(response.data.product.upload_file);
            }
           
            setProductOpen(true);
        })
    }

    const ProductModalClose = () => {
        setProductOpen(false);
    }

    const ProductModalSave = (data) => {
        ProductModalClose();
        setProductId(data);
        const frmData = new FormData();

        frmData.append('id', product_id);
        frmData.append('room_id', product_room_id);
        frmData.append('product_name', product_name);
        frmData.append('action', product_action);
        frmData.append('qty', product_qty);
        frmData.append('description', product_description);
        frmData.append('to_room_id', product_toroom_id);
        frmData.append('to_site_id', product_tosite_id);
        frmData.append('upload_file', product_file);
        $http.post(base_url + "product/updateProduct", frmData)
        .then((response) => {
            console.log(response.data);
            if (response.data.status === "success") {
                NotificationManager.success(response.data.msg, "Success!", 5000, () => {
                });
                Init_data();
            } else if (response.data.status === "error") {
                NotificationManager.error(response.data.msg, "Error!", 5000, () => {
                });
            }
        }).catch((error)=>{
            console.log("error:", error);
        })
    }
    
    return (
        <div className="app-wrapper">
            <ContainerHeader match={props.match} title={<IntlMessages id="sidebar.sitesurvey"/>}/>
            { loader ? (
                <div className="row">
                    <div className="col-12">
                        <div className="jr-card">
                            <div className="row">
                                <div className="col-xl-3 col-lg-3 col-md-6 col-sm-6 col-12">
                                    <Avatar className="size-120" alt="Remy Sharp" src={public_url + 'upload/img/' + Project.logo_img }/>
                                </div>
                                <div className="col-xl-4 col-lg-4 col-md-6 col-sm-6 col-12">
                                    <div className="row">
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-right">
                                            <h2><strong>Project Title</strong></h2>
                                        </div>
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-left">
                                            <h2>{Project.project_name}</h2>
                                        </div>
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-right">
                                            <h2><strong>Company Name</strong></h2>
                                        </div>
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-left">
                                            <h2>{Project.company_name}</h2>
                                        </div>
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-right">
                                            <h2><strong>Customer User</strong></h2>
                                        </div>
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-left">
                                            <h2>{Project.customer_user}</h2>
                                        </div>
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-right">
                                            <h2><strong>Contact Number</strong></h2>
                                        </div>
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-left">
                                            <h2>{Project.contact_number}</h2>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-xl-1 col-lg-1 col-md-6 col-sm-6 col-12"></div>
                                <div className="col-xl-4 col-lg-4 col-md-6 col-sm-6 col-12">
                                    <div className="row">
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-center">
                                            <h2><strong>Sites</strong></h2>
                                        </div>
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-left">
                                            <h2>{Project.site_count}</h2>
                                        </div>
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-center">
                                            <h2><strong>Rooms</strong></h2>
                                        </div>
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-left">
                                            <h2>{Project.room_count}</h2>
                                        </div>
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-center">
                                            <h2><strong>User Notifications</strong></h2>
                                        </div>
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-left">
                                            <h2>{Project.user_notifications}</h2>
                                        </div>
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-center">
                                            <h2><strong>Survey Date</strong></h2>
                                        </div>
                                        <div className="col-xl-6 col-lg-6 col-md-6 col-sm-6 col-12 text-left">
                                            <h2>{Project.survey_start_date}</h2>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-12">
                                    <Alert className="shadow-lg" color="primary" fullwidth="true">
                                        <h2><strong>Project Summary:</strong> {Project.project_summary}
                                        </h2>
                                    </Alert>
                                </div>
                                <ul className="list-inline d-sm-flex flex-sm-row jr-mbtn-list mb-0 jr-featured-content-right">
                                    <li><Button color="primary" onClick={() => ClickModify()}><IntlMessages id="button.modify"/></Button></li>
                                    <li><Button color="secondary" onClick={() => setWarning(true)}><IntlMessages id="button.delete"/></Button></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="jr-card">
                            <div className="jr-card-header d-flex align-items-center">
                                <h3 className="mb-0"><IntlMessages id="sidebar.customers.rooms"/></h3>
                                <div className="spacer"/>
                                <div className="actions">
                                    <Tooltip title="New Room">
                                    <IconButton aria-label="New Room" onClick={() => RoomModalOpen(null)}>
                                        <PlaylistPlayIcon/>
                                    </IconButton>
                                    </Tooltip>
                                </div>
                            </div>
                            <RoomTable Rooms={Rooms} onSelectRoom={onSelectRoom}/>
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="jr-card">
                            <div className="jr-card-header d-flex align-items-center">
                                <h3 className="mb-0"><IntlMessages id="sidebar.customers.sites"/></h3>
                                <div className="spacer"/>
                                <div className="actions">
                                    <Tooltip title="New Site">
                                    <IconButton aria-label="New Site" onClick={() => SiteModalOpen(null)}>
                                        <PlaylistPlayIcon/>
                                    </IconButton>
                                    </Tooltip>
                                </div>
                            </div>
                            <SiteTable Sites={Sites} onSelectSite={onSelectSite}/>
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="jr-card">
                            <div className="jr-card-header d-flex align-items-center">
                                <h3 className="mb-0"><IntlMessages id="sidebar.customers.products"/></h3>
                                <div className="spacer"/>
                                <div className="actions">
                                    <Tooltip title="New Site">
                                    <IconButton aria-label="New Site" onClick={() => ProductModalOpen(null)}>
                                        <PlaylistPlayIcon/>
                                    </IconButton>
                                    </Tooltip>
                                </div>
                            </div>
                            <ProductTable Products={Products} onSelectProduct={onSelectProduct}/>
                        </div>
                    </div>
                    <div className="col-12">
                        <div className="jr-card">
                            <div className="jr-card-header d-flex align-items-center">
                                <h3 className="mb-0"><IntlMessages id="sidebar.customers.task"/></h3>
                                <div className="spacer"/>
                                <div className="actions">
                                    <Tooltip title="New Task">
                                    <IconButton aria-label="New Task" onClick={() => TaskModalOpen(null)}>
                                        <PlaylistPlayIcon/>
                                    </IconButton>
                                    </Tooltip>
                                </div>
                            </div>
                            <TaskTable Tasks={Tasks} onSelectTask={onSelectTask}/>
                        </div>
                    </div>
                    <Modal key="site-save" className="modal-box" toggle={SiteModalClose} isOpen={site_open}>
                        <ModalHeader>{site_id ? 'Modify Site':'Add Site'}
                            <IconButton className="text-grey"
                                        onClick={SiteModalClose}>
                                <CloseIcon/>
                            </IconButton>
                        </ModalHeader>
                        <div className="modal-box-content">
                            <div className="row">
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="site_id"
                                        select
                                        label="Site"
                                        value={project_site}
                                        onChange={(event)=>setProjectSite(event.target.value)}
                                        SelectProps={{}}
                                        helperText="Please select Site"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {site_rows.map(option => (
                                            <MenuItem key={option.id} value={option.id}>
                                                {option.site_name}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                <div className="col-md-6 col-6 d-flex align-items-center">
                                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                        <KeyboardDatePicker
                                            margin="normal"
                                            id="task_duebydate"
                                            label="Due by Date"
                                            format="MM/dd/yyyy"
                                            value={site_date}
                                            onChange={setSiteDate}
                                            KeyboardButtonProps={{
                                                'aria-label': 'change date',
                                            }}
                                            helperText="Please Enter Due by Date"
                                            />
                                    </MuiPickersUtilsProvider>
                                </div>
                            </div>
                        </div>
                        <div className="modal-box-footer d-flex flex-row">
                            <Button onClick={SiteModalClose} color="secondary">
                            Cancel
                            </Button>
                            <Button onClick={SiteModalSave} color="primary">
                            Save
                            </Button>
                        </div>
                    </Modal>
                    <Modal key="room-save" className="modal-box" toggle={RoomModalClose} isOpen={room_open}>
                        <ModalHeader>{room_id ? 'Modify Room':'Add Room'}
                            <IconButton className="text-grey"
                                        onClick={RoomModalClose}>
                                <CloseIcon/>
                            </IconButton>
                        </ModalHeader>
                        <div className="modal-box-content">
                            <div className="row">
                                <div className="col-md-6 col-12">
                                    <TextField 
                                        id="room_project"
                                        label="Project"
                                        value={loader ? Project.project_name : ''}
                                        margin="normal"
                                        helperText="Please Enter Project"
                                        fullWidth
                                        disabled
                                        />
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="room_site"
                                        select
                                        label="Site"
                                        value={room_site}
                                        onChange={(event)=>setRoomSite(event.target.value)}
                                        SelectProps={{}}
                                        helperText="Please select Site"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {room_site_rows.map(option => (
                                            <MenuItem key={option.id} value={option.id}>
                                                {option.site_name}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="room_department"
                                        select
                                        label="department"
                                        value={room_department_id}
                                        onChange={(event)=>setRoomDepartmentName(event.target.value)}
                                        SelectProps={{}}
                                        helperText="Please select Department"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {room_departmentname_rows.filter(option => {
                                            return option.site_id == room_site
                                            }).map(option => (
                                            <MenuItem key={option.id} value={option.id}>
                                                {option.department_name}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="room_building"
                                        select
                                        label="building"
                                        value={room_building_id}
                                        onChange={(event)=>setRoomBuildingName(event.target.value)}
                                        SelectProps={{}}
                                        helperText="Please select Building"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {room_buildingname_rows.filter(option => {
                                            return option.department_id == room_department_id
                                            }).map(option => (
                                            <MenuItem key={option.id} value={option.id}>
                                                {option.building_name}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="room_floor"
                                        select
                                        label="floor"
                                        value={room_floor_id}
                                        SelectProps={{}}
                                        helperText="Please select Floor"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {room_floorname_rows.filter(option => {
                                            return option.building_id == room_building_id
                                            }).map(option => (
                                            <MenuItem key={option.id} value={option.id}>
                                                {option.floor_name}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField 
                                        id="room_number"
                                        label="Room Number"
                                        value={room_number}
                                        onChange={(event)=>setRoomNumber(event.target.value)}
                                        margin="normal"
                                        helperText="Please Enter Room Number"
                                        fullWidth
                                        />
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField 
                                        id="room_height"
                                        label="Ceiling Heights"
                                        value={room_height}
                                        onChange={(event)=>setRoomHeight(event.target.value)}
                                        margin="normal"
                                        helperText="Please Enter Ceiling Heights"
                                        fullWidth
                                        />
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField 
                                        id="room_ceiling"
                                        label="Ceiling"
                                        value={room_ceiling}
                                        onChange={(event)=>setRoomCeiling(event.target.value)}
                                        margin="normal"
                                        helperText="Please Enter Ceiling"
                                        fullWidth
                                        />
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField 
                                        id="room_wall"
                                        label="Wall"
                                        value={room_wall}
                                        onChange={(event)=>setRoomWall(event.target.value)}
                                        margin="normal"
                                        helperText="Please Enter Wall"
                                        fullWidth
                                        />
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="room_day"
                                        select
                                        label="Installation Estimate Days"
                                        value={room_day}
                                        onChange={(event)=>setRoomDay(event.target.value)}
                                        SelectProps={{}}
                                        helperText="Please select Days"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {[...Array(24)].map((id, value) => (
                                            <MenuItem key={value} value={value}>
                                                {value}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="room_hour"
                                        select
                                        label="Installation Estimate Hours"
                                        value={room_hour}
                                        onChange={(event)=>setRoomHour(event.target.value)}
                                        SelectProps={{}}
                                        helperText="Please select Days"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {[...Array(10)].map((id, value) => (
                                            <MenuItem key={value} value={value}>
                                                {value}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                <div className="col-md-6 col-12 d-flex align-items-center">
                                    
                                    <FormControl component="fieldset" required>
                                        <FormLabel component="legend">Asbestos</FormLabel>
                                        <RadioGroup
                                        className="d-flex flex-row"
                                        aria-label="gender"
                                        name="gender"
                                        value={room_asbestos}
                                        onChange={(event, value) => setRoomAsbestos(value)}
                                        >
                                        <FormControlLabel value="1" control={<Radio color="primary"/>} label="Yes"/>
                                        <FormControlLabel value="0" control={<Radio color="primary"/>} label="No"/>
                                        </RadioGroup>
                                    </FormControl>
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="room_notes"
                                        label="Notes"
                                        multiline
                                        rowsMax="4"
                                        value={room_notes}
                                        onChange={(event) => setRoomNotes(event.target.value)}
                                        margin="normal"
                                        fullWidth
                                    />
                                </div>
                                <div className="col-md-12 col-12 my-4 text-center">
                                      <input type="file" id="room_img" accept="image/*" multiple 
                                          name="room_img[]" style={{display: "none"}}
                                          onChange={(event) => {
                                              uploadMultipleFiles(event);
                                              //setRoomImg(event.target.files[0]);
                                          }}/>
                                      <Button className="jr-btn" color="primary"
                                          variant="contained" onClick={(e)=>{document.getElementById('room_img').click()}}>
                                          <AttachFile/> Add Image
                                      </Button>
                                  </div>
                                      {room_img_url.map( (url,index) => 
                                          <div className="col-xl-3 col-lg-4 col-md-4 col-6" key={index}>
                                          <div className="jr-card pb-2">
                                              <div className="jr-card-thumb">
                                                  <img className="card-img-top img-fluid" alt="products" src={url}/>
                                              </div>
                                              
                                              <div className="jr-card-social text-center">
                                                  
                                                  <Fab className="jr-fab-btn bg-light-blue accent-2 text-white"  onClick = {()=>removeImage(index)}>
                                                      <i className="zmdi zmdi-delete zmdi-hc-2x"/>
                                                  </Fab>
                                              </div>
                                          </div>
                                          </div>
                                      )}
                                
                            </div>
                        </div>
                        <div className="modal-box-footer d-flex flex-row">
                            <Button onClick={RoomModalClose} color="secondary">
                            Cancel 
                            </Button>
                            <Button onClick={RoomModalSave} color="primary">
                            Save
                            </Button>
                        </div>
                    </Modal>
                    <Modal key="product-save" className="modal-box" toggle={ProductModalClose} isOpen={product_open}>
                        <ModalHeader>{product_id ? 'Modify Product':'Add Product'}
                            <IconButton className="text-grey"
                                        onClick={ProductModalClose}>
                                <CloseIcon/>
                            </IconButton>
                        </ModalHeader>
                        <div className="modal-box-content">
                            <div className="row">                             
                               
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="product_roomname"
                                        select
                                        label="Room Name"
                                        value={product_room_id}
                                        onChange={(event)=>setProductRoomId(event.target.value)}
                                        SelectProps={{}}
                                        helperText="Please select Room Name"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {product_roomname_rows.map(option => (
                                            <MenuItem key={option.id} value={option.id}>
                                                {option.room_number}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField 
                                        id="product_name"
                                        label="Name"
                                        value={product_name}
                                        onChange={(event) => setProductName(event.target.value)}
                                        margin="normal"
                                        helperText="Please Enter Name"
                                        fullWidth
                                        />
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField 
                                        id="product_qty"
                                        label="Qty"
                                        value={product_qty}
                                        onChange={(event) => setProductQty(event.target.value)}
                                        margin="normal"
                                        helperText="Please Enter Qty"
                                        fullWidth
                                        />
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField 
                                        id="product_description"
                                        label="Description"
                                        value={product_description}
                                        onChange={(event) => setProductDescription(event.target.value)}
                                        margin="normal"
                                        helperText="Please Enter Description"
                                        fullWidth
                                        />
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="product_action"
                                        select
                                        label="Action"
                                        value={product_action}
                                        onChange={(event)=>setProductAction(event.target.value)}
                                        SelectProps={{}}
                                        helperText="Please select action"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {product_action_rows.map(option => (
                                            <MenuItem key={option.id} value={option.id}>
                                                {option.content}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="product_tosite"
                                        select
                                        label="To Site"
                                        value={product_tosite_id}
                                        onChange={(event)=>setProductToSiteId(event.target.value)}
                                        SelectProps={{}}
                                        helperText="Please select site"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {product_sitename_rows.map(option => (
                                            <MenuItem key={option.id} value={option.id}>
                                                {option.site_name}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="product_toroom"
                                        select
                                        label="Room"
                                        value={product_toroom_id}
                                        onChange={(event)=>setProductToRoomId(event.target.value)}
                                        SelectProps={{}}
                                        helperText="Please select To room"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {product_roomname_rows.map(option => (
                                            <MenuItem key={option.id} value={option.id}>
                                                {option.room_number}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                
                                <div className="col-md-6 col-12 d-flex align-items-center">
                                    <input type="file" id="upload_file" 
                                        accept="application/msword, application/vnd.ms-excel, 
                                        application/vnd.ms-powerpoint,text/plain, application/pdf" 
                                        name="upload_file" style={{display: "none"}}
                                        onChange={(event) => {
                                            setProductFile(event.target.files[0]);
                                            setProductFileUrl(event.target.files[0].name);
                                        }}/>
                                    <Button className="attach-file jr-btn " color = "primary"
                                        variant="contained" onClick={(e)=>{document.getElementById('upload_file').click()}}>
                                        <AttachFile/> Upload File
                                    </Button>
                                    <h3>{product_fileurl }</h3>
                                </div>
                                
                                   
                            </div>
                        </div>
                        <div className="modal-box-footer d-flex flex-row">
                            <Button onClick={ProductModalClose} color="secondary">
                            Cancel
                            </Button>
                            <Button onClick={ProductModalSave} color="primary">
                            Save
                            </Button>
                        </div>
                    </Modal>
                    <Modal key="task-save" className="modal-box" toggle={TaskModalClose} isOpen={task_open}>
                        <ModalHeader>{task_id ? 'Modify Task':'Add Task'}
                            <IconButton className="text-grey"
                                        onClick={TaskModalClose}>
                                <CloseIcon/>
                            </IconButton>
                        </ModalHeader>
                        <div className="modal-box-content">
                            <div className="row">                             
                                <div className="col-md-6 col-12">
                                    <TextField 
                                        id="task_project"
                                        label="Project"
                                        value={loader ? Project.project_name : ''}
                                        margin="normal"
                                        helperText="Please Enter Project"
                                        fullWidth
                                        disabled
                                        />
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="task_sitename"
                                        select
                                        label="Site Name"
                                        value={task_sitename}
                                        onChange={(event)=>setTaskSiteName(event.target.value)}
                                        SelectProps={{}}
                                        helperText="Please select Site Name"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {task_sitename_rows.map(option => (
                                            <MenuItem key={option.id} value={option.id}>
                                                {option.site_name}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="task_room"
                                        select
                                        label="Room"
                                        value={task_room}
                                        onChange={(event)=>setTaskRoom(event.target.value)}
                                        SelectProps={{}}
                                        helperText="Please select Room"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {Rooms.map(option => (
                                            <MenuItem key={option.id} value={option.id}>
                                                {option.room_number}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                <div className="col-md-6 col-12 d-flex align-items-center">
                                    <FormControl className="w-100">
                                        <InputLabel htmlFor="task-multiple">Assign To</InputLabel>
                                        <Select
                                            key="task_assignto"
                                            multiple
                                            value={task_assignto}
                                            onChange={(event) => setTaskAssignto(event.target.value)}
                                            input={<Input id="task-assign-to"/>}
                                            MenuProps={{
                                                PaperProps: {
                                                style: {
                                                    maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
                                                    width: 200,
                                                },
                                                },
                                            }}
                                            >
                                            {task_assignto_rows.map(item => (
                                                <MenuItem
                                                key={item.id}
                                                value={''+item.id}
                                                style={{
                                                    fontWeight: task_assignto_rows.indexOf(task_assignto_rows) !== -1 ? '500' : '400',
                                                }}
                                                >
                                                {item.first_name}
                                                </MenuItem>
                                            ))}
                                        </Select>
                                    </FormControl>

                                </div>
                                <div className="col-md-6 col-6 d-flex align-items-center">
                                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                        <KeyboardDatePicker
                                            margin="normal"
                                            id="task_duebydate"
                                            label="Due by Date"
                                            format="MM/dd/yyyy"
                                            value={task_duebydate}
                                            onChange={setTaskDuebydate}
                                            KeyboardButtonProps={{
                                                'aria-label': 'change date',
                                            }}
                                            helperText="Please Enter Due by Date"
                                            />
                                    </MuiPickersUtilsProvider>
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField 
                                        id="task_title"
                                        label="Task"
                                        value={task_title}
                                        onChange={(event) => setTaskTitle(event.target.value)}
                                        margin="normal"
                                        helperText="Please Enter Task"
                                        fullWidth
                                        />
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="task_des"
                                        label="Task Description"
                                        multiline
                                        rowsMax="4"
                                        value={task_des}
                                        onChange={(event) => setTaskDes(event.target.value)}
                                        margin="normal"
                                        fullWidth
                                    />
                                </div>
                                <div className="col-md-6 col-12">
                                    <TextField
                                        id="task_priority"
                                        select
                                        label="Priority"
                                        value={task_priority}
                                        onChange={(event)=>setTaskPriority(event.target.value)}
                                        SelectProps={{}}
                                        helperText="Please select Priority"
                                        margin="normal"
                                        fullWidth
                                    >
                                        {task_priority_rows.map(option => (
                                            <MenuItem key={option.id} value={option.id}>
                                                {option.content}
                                            </MenuItem>
                                        ))}
                                    </TextField>
                                </div>
                                <div className="col-md-6 col-12 d-flex align-items-center">
                                    <input type="file" id="upload_doc" 
                                        accept="application/msword, application/vnd.ms-excel, 
                                        application/vnd.ms-powerpoint,text/plain, application/pdf" 
                                        name="upload_doc" style={{display: "none"}}
                                        onChange={(event) => {
                                            setTaskImg(event.target.files[0]);
                                            setTaskImgUrl(event.target.files[0].name);
                                        }}/>
                                    <Button className="attach-file jr-btn " color = "primary"
                                        variant="contained" onClick={(e)=>{document.getElementById('upload_doc').click()}}>
                                        <AttachFile/> Upload File
                                    </Button>
                                </div>
                                <div className="col-md-6 col-12 d-flex align-items-center">
                                    <h2>{TaskImgUrl }</h2>
                                </div>
                            </div>
                        </div>
                        <div className="modal-box-footer d-flex flex-row">
                            <Button onClick={TaskModalClose} color="secondary">
                            Cancel
                            </Button>
                            <Button onClick={TaskModalSave} color="primary">
                            Save
                            </Button>
                        </div>
                    </Modal>
                    <NotificationContainer/>

                </div>
            ) :
            (
                <div className="loader-view"
                        style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                    <CircularProgress/>
                </div>                   
            )}
            <SweetAlert show={warning}
                        warning
                        showCancel
                        confirmBtnText={<IntlMessages id="sweetAlerts.yesDeleteIt"/>}
                        confirmBtnBsStyle="danger"
                        cancelBtnBsStyle="default"
                        title={<IntlMessages id="sweetAlerts.areYouSure"/>}
                        onConfirm={deleteFile}
                        onCancel={onCancelDelete}
            >
                You will not be able to recover this company!
            </SweetAlert>
        </div>
    );
}

export default SiteSurveyInfo;