import React from 'react';

import CardEditMenu from './CardEditMenu'

const ProductTableCell = (props) => {

    const {id, room_name, product_name,action, qty, to_site_name, to_room_name, description} = props.data;
    const {onSelectProduct} = props;
    const options = ([
        {content: 'Modify', id: 1},
        {content: 'Delete', id: 2}
    ]);

    return (
        <tr
            tabIndex={-1}
            key={id}
        >
            <td>{product_name}</td>
            <td>{action==0?'New Porduct':action==1?'Dispose':'Move To Room'}</td>
            <td>{room_name}</td>
            <td>{qty}</td>
            <td>{to_site_name}</td>
            <td>{to_room_name}</td>
            <td>{description}</td>
            <td className="text-right">
                <CardEditMenu key={id} selectedData={id} onSelectRow={onSelectProduct} menu_rows={options}/>
            </td>
        </tr>
    );
};

export default ProductTableCell;
